import React from "react";
import List from "@mui/material/List";
import { Card, CardHeader, Divider, TextField } from "@mui/material";
import RoomListItem from "./RoomListItem";
import {
  chatRoomComparer,
  getChatId,
  getChatName,
  isEmpty
} from "../utils/common";
import { useTranslation } from "react-i18next";
import { ChatRoom, Contact, Group, SetTyping, User } from "../types";
import { makeStyles } from "@mui/styles";

const useStyles = makeStyles(() => ({
  root: {
    width: "100%",
    height: "100%"
  },
  searchField: {
    width: "100%"
  }
}));

type RoomListProps = {
  apiUrl: string;
  user: User;
  activeRoom: ChatRoom | null;
  groups: Group[];
  contacts: Contact[];
  typing: SetTyping | null;
  onChangeChat?: (chat: ChatRoom) => void;
};

const filterChats = (chats: ChatRoom[], filter: string | null): ChatRoom[] => {
  if (filter === null) return chats;
  const lowerFilter = filter.toLowerCase();
  return chats.filter(
    chat =>
      getChatName(chat)
        .toLowerCase()
        .indexOf(lowerFilter.toLowerCase()) !== -1
  );
};

const sortChats = (
  userId: number,
  groups: ChatRoom[],
  contacts: ChatRoom[]
) => {
  let roomArr = [...groups, ...contacts];

  // –°–æ—Ä—Ç–∏—Ä–æ–≤–∞—Ç—å –æ–∫–Ω–æ —á–∞—Ç–∞ (–ø–æ –≤—Ä–µ–º–µ–Ω–∏ –ø–æ—Å–ª–µ–¥–Ω–∏—Ö —Å–æ–æ–±—â–µ–Ω–∏–π)
  roomArr = roomArr.sort(chatRoomComparer);

  // –ü—Ä–æ–≤–µ—Ä—è–µ–º, –µ—Å—Ç—å –ª–∏ —Å–ø–∏—Å–æ–∫, –∫–æ—Ç–æ—Ä—ã–π –Ω—É–∂–Ω–æ –∑–∞–∫—Ä–µ–ø–∏—Ç—å
  const topChatId = localStorage.getItem(`${userId}-topChatId`) as string;
  if (topChatId) {
    const chat = roomArr.find(c => getChatId(c) === topChatId);
    if (chat) {
      // –ù–∞ –ø–µ—Ä–≤–æ–µ –º–µ—Å—Ç–æ
      roomArr = roomArr.filter(k => getChatId(k) !== topChatId);
      chat.isTop = true;
      roomArr.unshift(chat);
    }
  }

  return roomArr;
};

const RoomList: React.FC<RoomListProps> = (props: RoomListProps) => {
  const classes = useStyles();
  const { t } = useTranslation();

  //  const [chats, setChats] = React.useState<ChatRoom[]>([]);

  let chats = sortChats(
    props.user.userId,
    filterChats(props.groups, null),
    filterChats(props.contacts, null)
  );

  const onSearchChange = (e: React.ChangeEvent<HTMLInputElement>): void => {
    chats = sortChats(
      props.user.userId,
      filterChats(props.groups, null),
      filterChats(props.contacts, null)
    );
  };

  //const activeItem = (id: number) => id === props.activeRoom?.userId;

  const Contatcs =
    !isEmpty(chats) &&
    chats.map(chat => (
      <RoomListItem
        key={getChatId(chat)}
        apiUrl={props.apiUrl}
        chat={chat}
        active={chat === props.activeRoom}
        typing={props.typing}
        onClick={() => props.onChangeChat != null && props.onChangeChat(chat)}
      />
    ));

  console.log("chats", chats);

  return (
    <Card elevation={1} className={classes.root}>
      <CardHeader
        title={
          <TextField
            className={classes.searchField}
            label={t("CHAT.INPUT_SEARCH_CONTACT")}
            variant="outlined"
            size="small"
            fullWidth
            onChange={onSearchChange}
          />
        }
      />
      <Divider />
      <List component="nav" aria-label="rooms">
        {!isEmpty(chats) ? Contatcs : <React.Fragment />}
      </List>
    </Card>
  );
};
export default RoomList;
